@first: #ea555c;

@bright_grey: #828282;
@dark_grey: #2e2e2e;

@third: #333;





.bluebox, .orangebox, .successbox, .infobox, .warningbox{
  .box_icon_large{
  font-size: 30px;
  }
  .box_heading_container{
    margin-top: 2px;
  }
}


/* BLUE */

.bluebox{
  width: 100%;
  background-color: white;
  border: 1px solid #00B3FF;
  margin-bottom: 20px;
  .bluebox_header{
  width: 100%;
  position: top;
  height: 60px;
  padding: 15px;
  color: white;
  background-color: #00B3FF;
  border-bottom: 1px solid #00B3FF;
  vertical-align: center;
  }
  .bluebox_content{
    padding: 15px; 
  }
}

