/* INFO */

.infobox{
  width: 100%;
  background-color: white;
  border: 1px solid #ababab;
  margin-bottom: 20px;
  .header{
  width: 100%;
  position: top;
  height: 60px;
  padding: 15px;
  background-color: #e8e8e8;
  border-bottom: 1px solid #ababab;
  vertical-align: center;
    p{
      color: @bright_grey;
    }
  }
  .content{
    padding: 15px;
    p{
      color: @first;
    }
    .info_text{
      color: @dark_grey;
    }
  }
  
}

/* WARNING */

.warningbox{
  width: 100%;
  background-color: white;
  border: 1px solid red;
  margin-bottom: 20px;
  .warningbox_header{
  width: 100%;
  position: top;
  height: 60px;
  padding: 15px;
  color: white;
  background-color: #ea555c;
  border-bottom: 1px solid #ea555c;
  vertical-align: center;
  }
  .warningbox_content{
    padding: 15px; 
  }
}

/* ACTION */

.actionbox{
  max-width: 200px;
  background-color: #ab4848;
  margin-top: 10px;
  padding: 20px;
  position: relative;
  margin-bottom: 20px;
  border: 1px solid #ab4848;
  i{
  margin-bottom: 10px;
  font-size: 40px;
  }
}


/* ACTIONBOX TALL */

.tall_actionbox{
  max-width: 200px;
  background-color: #ffa200;
  margin-top: 20px;
  padding: 20px;
  position: relative;
  margin-bottom: 20px;
  border: 1px solid #ababab;
  i{
  font-size: 40px;
  }
}

/* USER INFOBOX*/

.user_infobox{
  background-color: white;
  max-width: 202px;
  text-align: center;
  padding-top: 5px;
  padding-bottom: 20px;
  border-bottom: 1px solid #ababab;
  border-left: 1px solid #ababab;
  border-right: 1px solid #ababab;
}